<template>
  <transition name="slide">
    <div class="face-recognition-main">
      <c-nav-bar class="address-nav-bar">
        <i slot="left" class="nav-bar-left iconfont icon-fanhui1"></i>
        <span slot="center" class="nav-bar-center">
          人脸识别
        </span>
      </c-nav-bar>
      <div class="face-recogition-content">
        <video id="video" webkit-playsinline="true" playsinline="true"></video>
        <canvas id="canvas" width="375" height="320"></canvas>
        <div class="hole"></div>
        <div class="start-btn">
          <van-button v-show="!isBegin" size="large" @click="play" class="btn" type="default">
            开始识别
          </van-button>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  data() {
    return {
      isBegin: false
    }
  },
  mounted() {
    setTimeout(() => {
      this.init()
    },20)
  },
  methods: {
    init() {
      this.video = document.getElementById('video')
      this.canvas = document.getElementById('canvas')
      this.context = document.getElementById('canvas').getContext('2d')
      navigator.mediaDevices.getUserMedia({
        'audio': false,
        'video': { 'facingMode': 'user' } //调用前置摄像头，后置摄像头使用video: { facingMode: { exact: "environment" } }
      }).then((mediaStream)=>{
        this.getVideoStream(mediaStream)
      }).catch((err) => {
        alert(err)
      })
    },
    getVideoStream(mediaStream) {
      this.video.srcObject = mediaStream
    },
    play() {
      this.isBegin = true
      this.video.play();
      this.startDistinguish()
    },
    startDistinguish() {
      setTimeout(() => {
        this.getPicture()
      },2000)
    },
    getPicture() {
      this.context.drawImage(this.video, 0, 0, 375, 320)
      var base64 = this.canvas.toDataURL('image/jpeg', 1.0)
      var i = base64.indexOf(',');
      var content = base64.substr(i)
      this.$http.post(
        'https://dtplus-cn-shanghai.data.aliyuncs.com/face/verify',
        {
          type: 1,
          content_1: content,
          content_2: content,
        }
      ).then(res => {
        console.log(res)
        alert(JSON.stringify(res))
      },err => {
        console.log(err)
      })
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~@/css/mixin.scss';
.face-recognition-main {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index:1001;
  background: $background-color;
  .face-recogition-content {
    position: absolute;
    top: 44px;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    #video {
      margin: 0 auto;
      width: 100vw;
      height: 100vw;
      border-radius: 50%;
    }
    #canvas {
      display: none;
    }
    .hole {
      width: 100vw;
      height: 100vw;
      position: absolute;
      overflow: hidden;
      top: 0;
      left: 0;
      &:after {
        position: absolute;
        height: 70vw;
        width: 70vw;
        left: 50%;
        top: 50%;
        content: '';
        transform: translate(-50%,-50%);
        border-radius: 50%;
        border: 70vw solid #f5f5f5;
      }
    }
    .start-btn {
      padding: 0 15px;
      .btn{
        @include gradient();
        color:#fff;
        border-radius: 22px;
        font-size: 18px;
      }
    }
  }
}
</style>
